// main container
.navbar {
    background-color: white;
    height: 90px;
    box-shadow: 0px 3px 5px #8e8e8e33;

    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
 
    padding: .5rem 3rem;
}

// logo picture
.navbar-brand {
    padding-top: .3125rem;
    padding-bottom: .3125rem;
    margin-right: 1rem;
    font-size: 1.25rem;
    text-decoration: none;
    white-space: nowrap;

    img {
        vertical-align: middle;
        height: 40px;
    }
}

// container for links and dropdown buttons 
// formats everything onto the right side of the navbar
.navbar-items {
    margin-left: auto;
}

// regular links in navbar
.navbar-link {
    display: inline-block;
    font-family: "Poppins";
    font-size: 1.125rem;
    font-style: normal;
    font-weight: 500;
    letter-spacing: 0em;

    a {
        text-decoration: none;
        color: black;

        padding-left: 1vw;
        padding-right: 1vw;

        margin-left: 2.5vw;
        
    }
}

// container for dropdown button
.navbar-dropdown {
    display: inline-block;
    margin-left:  2.5vw;

    font-family: "Poppins";
    font-size: 1.125rem;
    font-style: normal;
    font-weight: 500;
    letter-spacing: 0em;
}

// actual dropdown button
.dropdown-button {
    border: none;
    cursor: pointer;
    background-color: white;

    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    letter-spacing: 0em;

    padding-left: 1vw;
    padding-right: 1vw;

    margin: 0;
}

// items/links that will show as the dropdown
.dropdown-items {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;

    a {
        text-decoration: none;
        color: black;
        display: block;
        padding: 16px 16px;
    }
}

// show the items when hovering over the dropdown
.navbar-dropdown:hover {
    .dropdown-items {
        display: block;
    }
}

.dropdown-items a:hover {background-color: #f1f1f1}
